<script>

export default {
  name: 'Account',
  data() {
    return {
      baseUrl: window.localStorage.getItem("baseUrl"),
      token: window.localStorage.getItem("token"),
      user: this.$store.getters.getUser,
      cloudinary256x256: 'https://res.cloudinary.com/dxlsenc2r/image/upload/c_thumb,h_256,w_256/',
    }
  },
  created() {
    window.StatusBar.overlaysWebView(false);  
    window.StatusBar.styleDefault();
    window.StatusBar.backgroundColorByHexString("#ffffff");

    window.cordova.plugin.http.get(this.baseUrl + "/user/api/profile", {}, { Authorization: "Bearer " + this.token }, (response) => {
      console.log(JSON.parse(response.data));
      this.$store.commit('setUser', JSON.parse(response.data));
      this.user = JSON.parse(response.data);
    }, (error) => {
      console.log(error);
    });
  },
  methods: {
    logout() {
      window.localStorage.removeItem('token');
      window.localStorage.removeItem('banned');
      this.$store.commit('resetState');
      this.$router.push({ name: 'Welcome' });
    },
    about() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'About' });
    },
    swipeWheel() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'SwipeWheel' });
    },
    listOrders(isOrder) {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListOrders', params: { 'isOrder': isOrder } });
    },
    listFollowing() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListFollowing' });
    },
    listMessages() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListMessages' });
    },
    listPartners() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListPartners' });
    },
    favoris() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'Favoris' });
    },
    listClips() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListClips' });
    },
    listPromotions() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'ListPromotions' });
    },
    wallet() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'Wallet' });
    },
    shop() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'Shop' });
    },
    addReview() {
      if (window.TapticEngine) {
        TapticEngine.impact({ style: 'medium' });
      }
      // window.cordova.plugins.AppReview.openStoreScreen(null, true);
    },
    goEditProfile() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'EditUser' });
    },
    goToPrelive() {
      this.addTapticAndSlide();
      if (this.user.vendor.products.length > 0) {
        this.$router.push({ name: 'PreLive' });
      } else {
        this.$router.push({ name: 'Shop' });
      }
    },
    goToVendorRegistration() {
      this.addTapticAndSlide();
      this.$router.push({ name: 'VendorRegistration' });
    },
    addTapticAndSlide() {
      if (window.TapticEngine) {
        TapticEngine.impact({ style: 'medium' });
      }
      window.plugins.nativepagetransitions.slide({
        direction: 'left',
        duration: 300,
        iosdelay: 0,
        androiddelay: 0,
        winphonedelay: 0,
      });
    },
    openUrl(url) {
      if (window.TapticEngine) {
        TapticEngine.impact({ style: 'medium' });
      }
      window.SafariViewController.isAvailable((available) => {
        if (available) {
          window.SafariViewController.show({ url: url }, (result) => {
            console.log(result);
          }, (error) => {
            console.log("KO: " + error);
          })
        } else {
          window.cordova.InAppBrowser.open(url, '_system', 'location=no');
        }
      });
    },
  }
};

</script>

